			        console.log('二维码内容：' + res.result);
					let str = atob(res.result);
					let strArr = str.split("cuit_chat:");
					console.log(str)
					uni.navigateTo({
						url: '../account/friends/frienddetails?type=search&condition=' + strArr[1]
					})
